import React, { useState } from 'react';
import { 
  Button,
  Form,
} from 'react-bootstrap';

import './App.css';
import classes from './MainStyling.module.css';
import 'bootstrap/dist/css/bootstrap.min.css';

function App() {
  const [ques, setQues] = useState('');
  const [flag, setFlag] = useState(false);
  const [answer, setAnswer] = useState('');
  const [submit, setSubmit] = useState(false);
  const [correct, setCorrect] = useState(false);

  const pledge = 'Dickhead please answer my question.';
  const responses = ["I don't trust you", "Try again some other time", "The semantic of the petition is incorrect", "You're not cool enough"];

  function handleChange(e) {
    const last_char = e.target.value.slice(-1)[0];
    const length = e.target.value.length;
    let display = e.target.value;
    
    if (!flag && last_char==='.')
    {
      setFlag(true);
      setCorrect(true);
      display = ques + pledge[length-1];
    }
    else if (flag && last_char==='.') {
      setFlag(false);
      display = ques + pledge[length-1];
    }
    else if (flag) {
      setAnswer(answer + last_char);
      display = ques + pledge[length-1];
    }
       
    setQues(display);
  }

  function handleSubmit(event) {
    event.preventDefault();
    setSubmit(true);
    if (!correct) {
      setAnswer(responses[Math.floor(Math.random() * responses.length)]);
    }
  }

  function reset() {
    window.location.reload(false);
  }

  return (
    <div className={classes.main}>
      <div className={classes.header}>
        <text style={styles.headerText}>
          Dickhead Answers
        </text>
      </div>
      <div className={classes.container}>
        <Form onSubmit={handleSubmit} >
          <Form.Group controlId="question">
            <Form.Label>Please enter your question</Form.Label>
            <Form.Control autoComplete="off" placeholder="Enter a question"/>
          </Form.Group>

          <Form.Group controlId="pledge">
            <Form.Label>Please enter your pledge!</Form.Label>
            <Form.Control autoComplete="off" placeholder="Dickhead please answer" onChange={handleChange} value={ques} />
          </Form.Group>
          <div style={{display: 'flex', justifyContent: 'space-around'}}>
            <Button variant="primary" type="submit">
              Ask
            </Button>
            <Button variant="danger" onClick={reset}>
              Retry
            </Button>
          </div>
        </Form>
        {submit&& <div style={{width:'100%', marginTop: 60, }} ><h3>{'answer: ' + answer}</h3></div>}    
      </div>
    </div>
  );
}

const styles = {
  headerText: {
    textShadow: '1px 2px 1px rgba(0,0,0,0.2)',
    fontFamily: 'monospace',
    fontWeight: 'bold',
    fontSize: '5em',
    color: 'white'
  }
}

export default App;
